    border-radius: 2rem;
    margin-bottom: 1rem;
  }
  
.badge{
    font-weight: 400;
    font-size: medium;
    /* background-color: #eff5f5; */
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    margin-bottom: .5rem;
    padding: .3rem 1rem;
    border-radius: 1.5rem;
  }

.blogimage{
    width: 100%;
}

  
.suggestions{
    color: #11b8ac;
    padding: .5rem;
    font-size: medium;
}

.sharebutton{
    padding: .5rem .7rem;
    border: 1px solid #11b8ac;
    color: #11b8ac;
    font-size: 1.3rem;
    border-radius: 2rem;
    margin-right: .5rem;
    text-decoration: none;
}
.sharebutton:hover{
  text-decoration: none;
    color: white;
    background-color: #11b8ac;
    cursor: pointer;
}
.highlight{
  background-color: #f4fafa;
  padding: 1rem;
  margin: 1.3rem 0;
  border-radius: .5rem;
  border:1px solid #11b8ac;
}







header {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px;
}

h1 {
  font-size: 32px;
}

.content {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.blog-content .blogsubheading {
  font-size: 24px;
  margin-top: 20px;
  font-family:Arial,Helvetica, sans-serif;
  color: #11b8ac;

}


.blog-content p {
  font-size: 1.2rem;
  line-height: 1.5;
  margin-bottom: 2rem;
  font-family: Arial, Helvetica, sans-serif;
}

.blog-content li {
  list-style-type: none; /* Remove default bullet points */
  padding: 0;
  font-size: 1.05rem;
  font-family: Arial, Helvetica, sans-serif;
}

.blog-content li::before {
  content: '\2713'; /* Unicode character for a checkmark symbol (you can replace with a different symbol) */
  color: #11b8ac; /* Color of the tick symbol */
  
  font-weight: bold;
  margin-right: 10px; /* Spacing between the tick and the list item */
}

.blog-content li b{
  color: #11b8ac;
}

.blog-content a{
  text-decoration: underline;
  color: #000000;
}

.blog-content a:hover{
  text-decoration: underline;
  color: #11b8ac;
}

.underline-class{
  text-decoration: underline;
}