/* CSS for landscape orientation (viewport width is greater than height) */
@media (orientation: landscape) {
  .blogimage{
    width: 100%;
    display: flex;
  }
  .desktoptest{
    display: flex;
  }
  .customnav{
    padding: 0 18rem;
  }
  .customfooter{
    /* background-color: #f0f0f0a4; */
    padding: 10rem 16rem;
  }
  #homepage-banner {
    display: block;
    /* Display the div in landscape mode */
  }
  

  .banner-text {
    font-size: 3rem;
  }

  #footerbottom {
    width: 90%;
  }

  #mobile-device-warning {
    display: none;
  }
}

/* CSS for portrait orientation (viewport width is less than height) */
@media (orientation: portrait) {
  
  .banner-image,.youtubediv{
    display: none;
  }
  .navtogglecontainer{
    margin-top: .3rem;
    padding: 2rem 1rem;
    border-bottom-left-radius: 1.5rem;
    border-bottom-right-radius: 1.5rem;
    background-color: #e9f1f1;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  }
 
  .searchbox{
    margin-top: 1rem;
  }
  body {
    margin: .7rem;
  }

  #homepage-banner {
    opacity: 0.01;
    /* Hide the div in portrait mode */
    margin: 2rem 0;
  }

  #footerbottom {
    width: 72%;
  }

  button {
    display: block;
    width: 100%;
    margin-bottom: 10px;
    padding: 10px 20px;
    font-size: 16px;
    /* color: #fff; */
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
}

