/* Customize the scrollbar's track (the area behind the thumb) */
::-webkit-scrollbar {
  width: .3rem;
}

/* Customize the scrollbar thumb (the draggable part) */
::-webkit-scrollbar-thumb {
  background: #11b8ac; /* Change the thumb color */
  border-radius: .2rem;
}

/* Customize the scrollbar track when hovered over */
::-webkit-scrollbar-thumb:hover {
  background: #11b8ac; /* Change the thumb color on hover */
}


