.footer {
  
  
  margin: .2rem;
    /* background-color: #f3f3f3e7; */
    padding: 20px 0;
    color: #00b6b6;
    font-size: 1.1rem;
    background-image: url('./background.jpg');
    background-size: cover;
    background-position: center right;

  }
  

  
  .social-icons {
    text-align: left;
    margin-top: 10px;
    
  }
  
  .social-icons a {
    color: #00b6b6;
    margin: 0 10px;
    font-size: 24px;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  .social-icons a:hover {
    color: #00bcd4; /* Change to your desired hover color */
  }
  
  table.table {
    border: none;
}

#footerbottom{
  color: #00b6b6;
}
td{
  color: #00b6b6;
}
