


    .popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .popup {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    text-align: center;
  }
  
  .popup h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .popup p {
    font-size: 18px;
    margin-bottom: 20px;
  }
  
  .close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    border: none;
    font-size: 24px;
    cursor: pointer;
  } 
   



  
  
  /* Show the close button only on small screens */
  @media (max-width: 768px) {
    .close-btn {
      display: none;
    }
  
    .close-btn-mobile {
      display: block;
      position: absolute;
      top: 10px;
      right: 10px;
      background-color: transparent;
      border: none;
      font-size: 24px;
      cursor: pointer;
    }
  }
  